@tailwind base;
@tailwind components;
@tailwind utilities;

@keyframes marquee {
    0% { transform: translateX(100%); }
    100% { transform: translateX(-100%); }
  }

  .animate-marquee {
    display: inline-block;
    padding-left: 100%;
    animation: marquee 15s linear infinite;
  }

/* Ocultar scrollbar en navegadores WebKit (Chrome, Safari) */
.scrollbar-hide::-webkit-scrollbar {
  display: none;
}

/* Ocultar scrollbar en Firefox */
.scrollbar-hide {
  scrollbar-width: none; /* Firefox */
}

/* Aplica a los scrollbars dentro de la clase custom-scrollbar */
.custom-scrollbar::-webkit-scrollbar {
  width: 8px;
}

.custom-scrollbar::-webkit-scrollbar-thumb {
  background-color: #1d4ed8; /* Color del scrollbar */
  border-radius: 4px;
}

.custom-scrollbar::-webkit-scrollbar-track {
  background-color: rgba(255, 255, 255, 0.1); /* Fondo del track */
  border-radius: 4px;
}

/* ===============================
   ESTILOS JW PLAYER MODERNO
   =============================== */

/* Ocultar elementos no deseados del JW Player */
.jw-icon-fullscreen, .jw-icon-pip, .jw-icon-cc, .jw-text-elapsed, .jw-icon-rewind {
  display: none !important;
}

/* Estilo de la barra de control principal con glassmorphism */
.jw-controlbar {
  background: rgba(23, 23, 23, 0.1) !important;
  backdrop-filter: blur(10px) !important;
  border-top: 1px solid rgba(115, 115, 115, 0.3) !important;
  box-shadow: 0 -4px 32px rgba(0, 0, 0, 0.4) !important;
}

/* Botones del player con efectos hover modernos */
.jw-icon {
  color: rgb(212, 212, 212) !important;
  transition: all 0.3s ease !important;
  border-radius: 6px !important;
  margin: 0 2px !important;
}

.jw-icon:hover {
  color: white !important;
  background: linear-gradient(135deg, rgba(29, 78, 216, 0.2), rgba(147, 51, 234, 0.2)) !important;

  border: 1px solid rgba(29, 78, 216, 0.5) !important;
  box-shadow: 0 4px 16px rgba(29, 78, 216, 0.25) !important;

}
/* Botón de play/pause con diseño especial */
.jw-icon-playback {
  background: rgba(29, 78, 216, 0.2) !important;
  border: 1px solid rgba(29, 78, 216, 0.3) !important;
  box-shadow: 0 2px 8px rgba(29, 78, 216, 0.2) !important;
}

.jw-icon-playback:hover {
  background: linear-gradient(135deg, rgba(29, 78, 216, 0.3), rgba(147, 51, 234, 0.3)) !important;
  box-shadow: 0 4px 20px rgba(29, 78, 216, 0.4) !important;
}

/* Barra de progreso con gradiente */
.jw-progress {
  background: linear-gradient(90deg, #1d4ed8, #9333ea) !important;
  box-shadow: 0 0 8px rgba(29, 78, 216, 0.4) !important;
  border-radius: 2px !important;
}

.jw-timesegment-progress {
  background: linear-gradient(90deg, #1d4ed8, #9333ea) !important;
}

/* Track de la barra de progreso */
.jw-progress-bar {
  background: rgba(115, 115, 115, 0.3) !important;
  border-radius: 2px !important;
}

/* Tiempo transcurrido y total */
.jw-text {
  color: rgb(212, 212, 212) !important;
  font-weight: 500 !important;
  text-shadow: 0 1px 2px rgba(0, 0, 0, 0.5) !important;
}

/* Control de volumen */
.jw-slider-volume .jw-slider-container {
  background: rgba(115, 115, 115, 0.3) !important;
}

.jw-slider-volume .jw-progress {
  background: linear-gradient(90deg, #1d4ed8, #9333ea) !important;
}

/* Tooltip personalizado */
.jw-tooltip {
  background: rgba(23, 23, 23, 0.95) !important;
  backdrop-filter: blur(10px) !important;
  border: 1px solid rgba(115, 115, 115, 0.3) !important;
  color: white !important;
  border-radius: 6px !important;
  box-shadow: 0 4px 16px rgba(0, 0, 0, 0.3) !important;
}

/* Overlay del player */
.jw-media {
  border-radius: 8px !important;
}

/* Display central (play button grande) */
.jw-display {
  background: rgba(0, 0, 0, 0.3) !important;
  backdrop-filter: blur(4px) !important;
  border-radius: 8px !important;
}

.jw-display-icon-container {
  background: rgba(29, 78, 216, 0.2) !important;
  backdrop-filter: blur(10px) !important;
  border: 2px solid rgba(29, 78, 216, 0.4) !important;
  border-radius: 50% !important;
  box-shadow: 0 8px 32px rgba(29, 78, 216, 0.3) !important;
  transition: all 0.3s ease !important;
}

.jw-display-icon-container:hover {
  background: rgba(29, 78, 216, 0.3) !important;
  border-color: rgba(29, 78, 216, 0.6) !important;
  box-shadow: 0 12px 40px rgba(29, 78, 216, 0.4) !important;
  transform: scale(1.05) !important;
}


@keyframes drawLine {
  0% {
    stroke-dashoffset: 300;
    opacity: 0.5;
  }
  50% {
    stroke-dashoffset: 0;
    opacity: 1;
  }
  100% {
    stroke-dashoffset: -300;
    opacity: 0.5;
  }
}

@keyframes bounce {
  0%, 20%, 53%, 100% {
    animation-timing-function: cubic-bezier(0.215, 0.61, 0.355, 1);
    transform: translate3d(0, 0, 0);
  }
  40%, 43% {
    animation-timing-function: cubic-bezier(0.755, 0.05, 0.855, 0.06);
    transform: translate3d(0, -8px, 0) scaleY(1.1);
  }
  70% {
    animation-timing-function: cubic-bezier(0.755, 0.05, 0.855, 0.06);
    transform: translate3d(0, -4px, 0) scaleY(1.05);
  }
  80% {
    transition-timing-function: cubic-bezier(0.215, 0.61, 0.355, 1);
    transform: translate3d(0, 0, 0) scaleY(0.95);
  }
  90% {
    transform: translate3d(0, -2px, 0) scaleY(1.02);
  }
}